






















































import { Component, Prop, Vue } from 'vue-property-decorator';
import MainPageHeader from '../components/MainPageHeader.vue';
import BigButton from '../components/input/BigButton.vue';
import SmallButton from '../components/input/SmallButton.vue';
import AuthButton from '../components/input/AuthButton.vue';
import DefaultInputField from '../components/input/DefaultInputField.vue';
import PopupBackground from '../components/PopupBackground.vue';
import PolicyRow from '../components/PolicyRow.vue';

@Component({
    name: 'CreateAccountPage',
    components: {
        MainPageHeader,
        BigButton,
        SmallButton,
        AuthButton,
        DefaultInputField,
        PopupBackground,
        PolicyRow,
    },
})
export default class CreateAccountPage extends Vue {
    email: string = '';
    password: string = '';
    passwordRepeat: string = '';
    policyStates: boolean[] = [ false, false, false ];

    get areAllChecked() {
        return !this.policyStates.some(p => p == false);
    }

    acceptAllInput() {
        if (this.policyStates.some(p => p == false)) {
            this.policyStates = this.policyStates.map(p => true);
        } else {
            this.policyStates = this.policyStates.map(p => false);
        }
    }

    policyInput(index: number, state: boolean) {
        const states = this.policyStates.slice();
        states[index] = !this.policyStates[index];
        this.policyStates = states;
    }
}
